import { ChangeDetectorRef, SimpleChange, SimpleChanges } from '@angular/core';
import { ComponentFixture, flush } from '@angular/core/testing';

export function testDetectChanges<T>(cd: ChangeDetectorRef, fixture: ComponentFixture<T>) {
  cd.markForCheck();
  fixture.autoDetectChanges();
  flush();
}

export function createTestSimpleChanges<T>(data: T, dataName: string): SimpleChanges {
  const simpleChange = new SimpleChange(undefined, data, true);
  return { [dataName]: simpleChange } as SimpleChanges;
}
